#start-body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  overflow: hidden;
  padding: 0;
  margin: 0;
}

.glowing {
  position: relative;
  width: 70%;
  height: 70%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
}

.glowing img {
  width: 110%;
  margin-top: -5%;
  animation: bounce 2s ease-in-out infinite;
}

.glowing .sparkle1 {
  position: absolute;
  top: -10%;
  right: 10%;
  animation: twinkle 1s ease-in-out infinite;
}

.glowing .sparkle2 {
  position: absolute;
  top: 0;
  left: 0;
  animation: twinkle 1.6s ease-in-out infinite;
}

.glowing .sparkle3 {
  position: absolute;
  top: 60%;
  left: -10%;
  animation: twinkle 2s ease-in-out infinite;
}

.glowing .sparkle4 {
  position: absolute;
  bottom: 10%;
  right: 0;
  animation: twinkle 1.2s ease-in-out infinite;
}

.glowing .sparkle5 {
  position: absolute;
  bottom: -10%;
  left: 30%;
  animation: twinkle .7s ease-in-out infinite;
}

.glowing span:before {
  content: '';
  position: absolute;
  top: 50%;
  left: -8px;
  width: 15px;
  height: 15px;
  background-color: white;
  border-radius: 50%;
  box-shadow: 0 0 20px #fff,
              0 0 40px #fff,
              0 0 60px#fff,
              0 0 80px #fff,
              0 0 0 8px rgba(255, 255, 255, 0.048);
}

.start-btn {
  background-color: #4A3DD8;
  position: relative;
  margin-top: -50px;
  width: 100px;
  box-shadow: 5px 4px 0px #170D87;
}

@keyframes bounce {
  50% { transform: translateY(10px);}
}

@keyframes twinkle {
  0% { opacity: .7; }
  25% { opacity: .6;}
  50% { opacity: .5;}
  75% { opacity: .4;}
  100% { opacity: .6;}
}

@media only screen and (min-width: 768px) {
  .glowing img {
    width: 50%;
  }
}
