.extra-xp {
  font-size: 13px;
  text-align: right;
  margin-top: -15px;
  margin-bottom: 15px;
  font-weight: bold;
  color: #FFC125;
  -webkit-text-stroke: 1px #CC7A00;
  cursor: pointer;
  position: relative;
  text-transform: none;
}

.extra-xp .popuptext {
  visibility: hidden;
  display: none;
  width: 221px;
  background-color: rgba(0, 0, 0, 0.747);
  color: #fff;
  font-family: 'Roboto', sans-serif;
  font-size: 12px;
  -webkit-text-stroke: 0px;
  text-align: left;
  padding: 8px 8px;
  position: absolute;
  z-index: 0;
  bottom: -320%;
  left: 30%;
}

.extra-xp .popuptext::after {
  content: "";
  position: absolute;
  top: -20%;
  left: 80%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent rgba(0, 0, 0, 0.747) transparent;
}

.extra-xp .show {
  visibility: visible;
  display: block;
  animation: fadeIn 1s
}

hr {
